



































































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Emit,
  Watch
} from "vue-property-decorator";
import { isURL } from "validator";
import { LocaleMessages } from "vue-i18n";
import { repositoriesModule } from "@/store/modules";
import { Overwrite, RepoType } from "@/shared";
import { IRepository } from "@/store/modules/RepositoriesModule";
import FolderSelectorDialog from "@/components/misc/FolderSelectorDialog.vue";

type RepositoryOptionalId = Overwrite<
  IRepository,
  {
    id?: number;
  }
>;

@Component({
  components: {
    FolderSelectorDialog
  }
})
export default class RepositoryEditorComponent extends Mixins() {
  maxNameLength = 100;
  maxPasswordLength = 100;
  valid = true;
  name = "";
  password = "";
  location = "";
  s3AccessKey = "";
  s3SecretKey = "";
  repoType = RepoType.S3;
  autoUnlockDelay = 100;
  locationSelectorOpen = false;

  @Watch("s3AccessKey")
  trimAccessKey() {
    if (this.s3AccessKey.length > 20) {
      this.s3AccessKey = this.s3AccessKey.substr(0, 20);
    }
  }

  @Watch("s3SecretKey")
  trimSecretKey() {
    if (this.s3SecretKey.length > 40) {
      this.s3SecretKey = this.s3SecretKey.substr(0, 40);
    }
  }

  @Prop({
    type: Object,
    default: () =>
      ({
        name: "",
        password: "",
        location: "",
        s3AccessKey: "",
        s3SecretKey: "",
        type: RepoType.S3,
        autoUnlockDelay: 100
      } as RepositoryOptionalId)
  })
  initialData!: RepositoryOptionalId;

  @Watch("initialData", { immediate: true })
  updateLocalData() {
    this.name = this.initialData.name;
    this.password = this.initialData.password;
    this.location = this.initialData.location;
    this.s3AccessKey = this.initialData.s3AccessKey;
    this.s3SecretKey = this.initialData.s3SecretKey;
    this.repoType = this.initialData.type;
    this.autoUnlockDelay = this.initialData.autoUnlockDelay;
  }

  get isLocalRepository() {
    return this.repoType === RepoType.Local;
  }

  get repositoryTypes() {
    return [
      { text: this.$t("repository-editor.type.s3"), value: RepoType.S3 },
      {
        text: this.$t("repository-editor.type.local"),
        value: RepoType.Local
      }
    ];
  }

  async submit() {
    if ((this.$refs.form as any).validate()) {
      this.formSubmit({
        name: this.name,
        password: this.password,
        location: this.location,
        s3AccessKey: this.s3AccessKey,
        s3SecretKey: this.s3SecretKey,
        id: this.initialData.id, // undefined for new repositories
        type: this.repoType,
        autoUnlockDelay: this.autoUnlockDelay
      });
    }
  }

  @Emit("form-submit")
  formSubmit(payload: RepositoryOptionalId) {}

  async clear() {
    this.$nextTick(() => {
      this.updateLocalData();
    });
  }

  get validateRules(): {
    [idx: string]: ((val: string) => boolean | string | LocaleMessages)[];
  } {
    // regex for keys from https://aws.amazon.com/blogs/security/a-safer-way-to-distribute-aws-credentials-to-ec2/
    return {
      s3AccessKey: [
        v => !!v || this.$t("repository-editor.s3-access-key-required"),
        v =>
          (v && /^([A-Z0-9]{20})$/g.test(v)) ||
          this.$t("repository-editor.s3-access-key-invalid")
      ],
      s3SecretKey: [
        v => !!v || this.$t("repository-editor.s3-secret-key-required"),
        v =>
          (v && /^([A-Za-z0-9/+=]{40})$/g.test(v)) ||
          this.$t("repository-editor.s3-secret-key-invalid")
      ],
      location: [
        v => !!v || this.$t("repository-editor.location-required"),
        v =>
          // only test for valid url if repo type is s3
          this.isLocalRepository ||
          (v &&
            isURL(v, {
              require_tld: false
            })) ||
          this.$t("repository-editor.invalid-url")
      ],
      password: [
        v => !!v || this.$t("repository-editor.password-required"),
        v =>
          (v && v.length <= this.maxPasswordLength) ||
          this.$t("repository-editor.password-length", {
            length: this.maxPasswordLength
          })
      ],
      name: [
        v => !!v || this.$t("repository-editor.name-required"),
        v =>
          (v && v.length <= this.maxNameLength) ||
          this.$t("repository-editor.name-length", {
            length: this.maxPasswordLength
          })
      ],
      repoType: [v => !!v || this.$t("repository-editor.type-required")],
      autoUnlockDelay: [
        v => !!v || this.$t("repository-editor.auto-unlock-delay-required")
      ]
    };
  }
}
