





import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import RepositoryEditor from "@/components/misc/RepositoryEditor.vue";
import { Overwrite } from "@/shared";
import { IRepository } from "@/store/modules/RepositoriesModule";
import { repositoriesModule, routerModule } from "@/store/modules";

@Component({
  components: {
    RepositoryEditor
  }
})
export default class EditRepositoryView extends Mixins() {
  get repoId(): string {
    return routerModule.params.repositoryId;
  }

  get repoData() {
    return repositoriesModule.getById(this.repoId);
  }

  async onSubmit(repo: IRepository) {
    // TODO: infer args from other class
    // TODO: trycatch
    const resp = await repositoriesModule.updateRepository(repo);
    if (resp && resp.payload) {
      this.$router.replace({
        path: `/repositories/${resp.payload.repo.id}`
      });
    }
  }
}
